* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'open sans', sans-serif;
  font-size: 20px;
}

body{
  font-family:Arial;
  font-size:11px;
  line-height:18px;
  color:#a7a7a7;
  padding:30px 0 0 0; 
  background:#f3f3f3;
}

.wrapper{
  max-width: 90vw;
  margin:0 auto; 
  background:#ebebeb; 
  padding:4px;
}

img,a img{
  border:0;
}

ul, li, dl, dt, dd{
  list-style:none;
}

a{
  text-decoration:none; 
  color:#a7a7a7;
}

a:hover{ 
  text-decoration: unset;
}

.clear{
  clear:both; 
  height:0; 
  overflow:hidden; 
  font-size:0; 
  line-height:0; 
  border:0;}

.clearfix:after {
  clear:both; 
  height:0; 
  overflow:hidden; 
  display:block; 
  visibility:hidden; 
  content:"."; 
}

textarea,input,button,select{
  font-size:12px; 
  vertical-align:middle; 
  color:#666;
  padding:1px;
  font-family:Arial;
}


.header{
    height:7em; 
    position:relative; 
    background:#fff;
}

.header h1 a{
    position:relative; 
    cursor:pointer; 
    left: 30px; top: 30px; 
    text-transform: uppercase; 
    font-size: 33px; font-family: 
    sans-serif; 
    color:lightslategray
}

.header p strong{ 
    height:30px; 
    display:inline-block;
    padding:2px 10px; 
    font-size:12px; 
    color:#7ba6c7;
}

.header p{
    position:absolute; 
    left:250px; 
    top:40px;
}


@media only screen and (max-width:620px) {
    .header, .header h1 a, .header p {
        width: 100%;
    }

    .header p strong {
        position: relative;
        left: -40px;
        top: 15px;
    }
}
.sidebar{
    float:right; 
    width: 30%;
    text-align: left;
}

.sidebar .mob{
    margin:4px 0px 4px 0px; 
    padding:5px; 
    widows: 100em;
    position:relative; 
    background:#fff;
}

@media only screen and (max-width:955px) {
    .sidebar, .sidebar .mob {
        width: 100%;
    }


}
.sidebar .mob h2{
    font-size:18px; height:24px; 
    line-height:20px; 
    border-bottom:3px solid #a7a7a7; 
    color:#7ba6c7;
}

.sidebar .mob ul{
    padding-bottom:5px;
}

.sidebar .mob li a{
    display:block; 
    color:#9e9e9e; 
    font-size:12px; 
    height:31px; 
    line-height:31px; 
    padding:0 0 0 10px; 
    border-bottom:1px solid #deddde; 
}

.get_method {
	text-transform: uppercase;
	color: #3872FF;
	text-shadow: 0.5px 0.5px black;
}

.put_method {
	text-transform: uppercase;
	color: #f68341;
	text-shadow: 0.3px 0.3px black;
}

.post_method {
	text-transform: uppercase;
	color: #14c51d;
	text-shadow: 0.3px 0.3px black;
}

.delete_method {
	text-transform: uppercase;
	color: #c60303;
	text-shadow: 0.3px 0.3px black;
}

.patch_method {
	text-transform: uppercase;
	color: #f68341;
	text-shadow: 0.3px 0.3px black;
}

.disabled {
	pointer-events: none;
	cursor: default;
}
.content{
    float:left; 
    width: 69.7%;
}

.content .post{
    margin-top:4px; 
    position:relative; 
    padding:5px;
    background:#fff; 
}

@media only screen and (max-width:955px) {
    .content, .content .post {
        width: 100%;
    }
}

.content .post h2{ 
    height:30px; 
    line-height:30px; 
    font-size:21px; 
    border-bottom:3px solid #e0e0e0; 
    padding-left: 5px;
}

.content .post p.a{
    height:22px; 
    padding-left: 5px; 
    line-height:22px; 
    background:#f3f3f3; 
    color:#848484; 
    font-size: 13px; 
    font-family: sans-serif; 
    color:#7ba6c7;
}

.content .post p.a a{
    color:#848484;
}

.content .post p.a span{
    padding:0 10px; 
}

.content .post .under_title{
    font-size: 15px; 
    margin: 5px 5px; 
    color: black; 
    display: inline-block;
}

.content .post .details{
    padding:5px 5px;
}

pre code {
	background: grey;
    border: 1px solid black;
    color: white;
    page-break-inside: avoid;
    font-size: 1.3em;
    line-height: 1.6;
	margin-bottom: 1.6em;
    max-width: 100%;
    overflow: auto;
    padding: 1em 1em;
    display: block;
	word-wrap: break-word;
  }

  .info p {
	  padding-left: 5px;
	  padding-top: 5px;
	  font-size: 15px;
	  color: black;
  }

  .text_wrapper p {
	padding-top: 5px;
	font-size: 15px;
	color: black;
	margin: 5px;
	font-family: serif;
	font-size: 16px;
	text-align: justify;
	letter-spacing: 0.5px;
	margin: 5px 10px;
	line-height: 1.2;
	word-spacing: 0.5px;
}

.text-line {
	font-size: 20px;
	margin: 5px 5px;
	padding: 5px 5px;
}
.footer{
    clear:both; 
    width:100%;
    margin:0 auto; 
    text-align:center; 
    height:30%;
    background: white;
}

.footer .top{
    padding:10px; 
    color:#828282;
}

@media only screen and (max-width:955px) {
    .footer , .footer .top {
        width: 100%;
    }
}
